import React from 'react'
import { graphql, StaticQuery } from 'gatsby'
import moment from 'moment'
import PattyWorkingAtDesk from './patty-working-at-desk'
import CategoryIcon from './category-icon'

const Footer = () => {
  return (
    <StaticQuery
      query={graphql`
        {
          site {
              siteMetadata {
                  title
                  description
              }
          }
          areas: allMarkdownRemark(filter: {frontmatter: {type: {eq: "area"}}}, sort: {fields: [frontmatter___name]}) {
            edges {
              node {
                fields {
                  slug
                }
                frontmatter {
                  name
                  tag
                }
              }
            }
          }
          categories: allMarkdownRemark(filter: {frontmatter: {type: {eq: "category"}}}) {
            edges {
                node {
                    frontmatter {
                        tag
                        name
                        icon
                    }
                    fields {
                        slug
                    }
                }
            }
        }
        }
      `}
      render={data => {
        return (
          <footer className="container-fluid mt-5 pb-3 footer">
            <div className="row pt-5">
              <div className="col-lg mb-5 pr-5">
                <h2 className="title--header small mb-4"><a className="link brand" rel="author"
                                                       href="/">{data.site.siteMetadata.title}</a></h2>
                <p>
                  <small>{data.site.siteMetadata.description}</small>
                </p>
                <PattyWorkingAtDesk/>
                <p><em>
                  <small>All rights reserved. &copy; expatty.com {moment().format('YYYY')}</small>
                </em></p>
              </div>
              <div className="col-lg mb-5">
                <h2 className="title--header small primary-colour mb-4">Areas</h2>
                <ul>
                  {
                    data.areas.edges.map(({ node }, idx) => (
                      <li key={idx}><span className="spacer"/> <a href={node.fields.slug} className="link">{node.frontmatter.name}</a></li>
                    ))
                  }
                </ul>
              </div>
              <div className="col-lg mb-5">
                <h2 className="title--header small primary-colour mb-4">Categories</h2>
                <ul>
                  {
                    data.categories.edges.map(({ node }, idx) => (
                      <li key={idx}>
                        <CategoryIcon path={node.fields.slug} name={node.frontmatter.name}
                                      icon={node.frontmatter.icon}/><a href={node.fields.slug}
                                                                       className="link"> {node.frontmatter.name}
                      </a></li>
                    ))
                  }
                </ul>
              </div>
              <div className="col-lg mb-5">
                <h2 className="title--header small primary-colour mb-4">Subscribe to our newsletter</h2>
                <form
                  action="https://expatty.us7.list-manage.com/subscribe/post?u=a6b6c49a554f762c8c4c8d495&amp;id=1eef58f2b5"
                  method="post" id="mc-embedded-subscribe-form" name="mc-embedded-subscribe-form" className="validate"
                  target="_blank" noValidate>
                  <div className="form-group">
                    <input type="email" name="EMAIL" className="form-control" id="mce-EMAIL"
                           placeholder="email address" required/>
                  </div>
                  <div style={{ position : 'absolute', left: '-5000px'}} aria-hidden="true">
                    <input type="text"
                           name="b_a6b6c49a554f762c8c4c8d495_1eef58f2b5"
                           tabIndex="-1"/>
                  </div>
                  <div className="clear">
                    <button type="submit" value="Subscribe" name="subscribe" id="mc-embedded-subscribe"
                            className="btn">Subscribe
                    </button>
                  </div>
                </form>
              </div>
            </div>
          </footer>
        )
      }}/>
  )
}

export default Footer